import CircularProgress from '@/components/circles/CircularProgress';
import { PrimaryButton } from '@/components/buttons/PrimaryButton';
import { SecondaryButton } from '@/components/buttons/SecondaryButton';
import { SubtleButton } from '@/components/buttons/SubtleButton';
import EditIcon from '@/components/icons/EditIcon';
import BlockIcon from '@/components/icons/BlockIcon';
import UnderlineHeading from '@/components/heading/UnderlineHeading';
import { Link } from 'react-router-dom';
import Heading from '@/features/customerRelations/components/viewCustomer/Heading';
import adel from '@/assets/imgs/user.png';
import { Employee } from '@/types/employee.types';
import { format } from 'date-fns';

interface Props {
    employee: Employee | null;
}
export default function EmployeeInfo({ employee }: Props) {
    return (
        <div>
            <Heading />
            <div className="flex relative flex-row justify-between px-4 bg-primery-blue-800 w-full text-white-blue-50 rounded-2xl">
                <div className="absolute bottom-0 translate-y-[53%]">
                    <div className="flex flex-col items-center justify-center">
                        <div className="w-[242px] h-[242px] rounded-full overflow-hidden">
                            {employee?.personal_image ? (
                                <img
                                    // src={employee.profile_img}
                                    src={adel}
                                    alt="Employee Image"
                                    className="w-full h-full object-cover"
                                />
                            ) : (
                                <div className="flex items-center justify-center h-full bg-gray-300">
                                    <p className="text-white-blue-50 text-8xl font-normal tracking-normal">
                                        {employee?.name
                                            .split(' ')
                                            .map((name) => name[0])
                                            .join('')}
                                    </p>
                                </div>
                            )}
                        </div>
                    </div>
                </div>
                <div className="ms-[242px] ps-6 py-4">
                    <p className="text-start font-bold text-4xl leading-none tracking-normal py-3 mb-3">
                        {employee?.name}
                    </p>
                    <p className="text-start font-medium text-xl leading-none tracking-normal mt-2 mb-1">عميل</p>
                </div>
                <div className="my-[5.5px] flex flex-col justify-between items-center">
                    <p className="font-medium text-xs tracking-normal">التقييم</p>
                    <CircularProgress
                        textClassName="text-white-blue-50"
                        progressClassName="stroke-white-blue-50"
                        backgroundClassName="stroke-white-blue-700"
                        size={48}
                    />
                    <p className="font-medium text-xs tracking-normal border border-white-blue-50 p-1 rounded-sm">
                        مقبول
                    </p>
                </div>
            </div>
            <div className="flex flex-row">
                <div>
                    <div className="flex flex-col w-[242px] gap-6 ms-4 mt-[150px]">
                        <PrimaryButton>إرسال رسالة</PrimaryButton>
                        <Link to={`/employee-relations/edit-employee/${employee?.id}`}>
                            <SecondaryButton className="bg-primery-blue-50 w-full">
                                <EditIcon />
                                تعديل
                            </SecondaryButton>
                        </Link>
                        <SubtleButton className="bg-red-error-50 text-[#A21515]">
                            <BlockIcon />
                            حظر
                        </SubtleButton>
                    </div>
                </div>
                <div className="flex flex-row gap-6 justify-start">
                    <div className="ms-6 mt-5 w-full">
                        <UnderlineHeading text="البيانات الأساسية" />
                        <div className="w-full mt-2 min-w-[352px]">
                            <div className="flex flex-row items-center border-b border-b-white-blue-500">
                                <p className="min-w-32 bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[128px]">
                                    الاسم الكامل
                                </p>
                                <p className="flex flex-row items-center gap-4 p-2 w-full text-primery-blue-900 text-base font-medium">
                                    {employee?.name}
                                </p>
                            </div>
                            <div className="flex flex-row items-center border-b border-b-white-blue-500">
                                <p className="min-w-32 bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[128px]">
                                    تاريخ الميلاد
                                </p>
                                <p className="flex flex-row items-center gap-4 p-2 w-full text-primery-blue-900 text-base font-medium">
                                    {employee?.birth_date}
                                </p>
                            </div>
                            <div className="flex flex-row items-center border-b border-b-white-blue-500">
                                <p className="min-w-32 bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[128px]">
                                    رقم الهوية
                                </p>
                                <p className="flex flex-row items-center gap-4 p-2 w-full text-primery-blue-900 text-base font-medium">
                                    {employee?.id_number}
                                </p>
                            </div>
                            <div className="flex flex-row items-center border-b border-b-white-blue-500">
                                <p className="min-w-32 bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[128px]">
                                    العنوان
                                </p>
                                <p className="flex flex-row items-center gap-4 p-2 w-full text-primery-blue-900 text-base font-medium">
                                    {employee?.address}
                                </p>
                            </div>
                            <div className="flex flex-row items-center border-b border-b-white-blue-500">
                                <p className="min-w-32 bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[128px]">
                                    الهاتف
                                </p>
                                <p className="flex flex-row items-center gap-4 p-2 w-full text-primery-blue-900 text-base font-medium">
                                    {employee?.phone_number}
                                </p>
                            </div>
                            <div className="flex flex-row items-center border-b border-b-white-blue-500">
                                <p className="min-w-32 bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[128px]">
                                    البريد الإلكتروني
                                </p>
                                <p className="flex flex-row items-center gap-4 p-2 w-full text-primery-blue-900 text-base font-medium">
                                    {employee?.email}
                                </p>
                            </div>
                        </div>
                    </div>

                    <div className="ms-6 mt-5 w-full">
                        <UnderlineHeading text="البيانات الوظيفية" />
                        <div className="w-full mt-2 min-w-[352px]">
                            <div className="flex flex-row items-center border-b border-b-white-blue-500">
                                <p className="min-w-32 bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[128px]">
                                    كود الموظف
                                </p>
                                <p className="flex flex-row items-center gap-4 p-2 w-full text-primery-blue-900 text-base font-medium">
                                    {employee?.employee_number}
                                </p>
                            </div>
                            <div className="flex flex-row items-center border-b border-b-white-blue-500">
                                <p className="min-w-32 bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[128px]">
                                    القسم
                                </p>
                                <p className="flex flex-row items-center gap-4 p-2 w-full text-primery-blue-900 text-base font-medium">
                                    {employee?.department}
                                </p>
                            </div>
                            <div className="flex flex-row items-center border-b border-b-white-blue-500">
                                <p className="min-w-32 bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[128px]">
                                    الدرجة الوظيفية
                                </p>
                                <p className="flex flex-row items-center gap-4 p-2 w-full text-primery-blue-900 text-base font-medium">
                                    {employee?.position_name}
                                </p>
                            </div>
                            <div className="flex flex-row items-center border-b border-b-white-blue-500">
                                <p className="min-w-32 bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[128px]">
                                    نوع التعاقد
                                </p>
                                <p className="flex flex-row items-center gap-4 p-2 w-full text-primery-blue-900 text-base font-medium">
                                    {employee?.contract_type}
                                </p>
                            </div>
                            <div className="flex flex-row items-center border-b border-b-white-blue-500">
                                <p className="min-w-32 bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[128px]">
                                    تاريخ التعيين
                                </p>
                                <p className="flex flex-row items-center gap-4 p-2 w-full text-primery-blue-900 text-base font-medium">
                                    {employee?.created_at ? format(employee?.created_at, 'yyyy-MM-dd') : null}
                                </p>
                            </div>
                            <div className="flex flex-row items-center border-b border-b-white-blue-500">
                                <p className="min-w-32 bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[128px]">
                                    تاريخ الترقية
                                </p>
                                <p className="flex flex-row items-center gap-4 p-2 w-full text-primery-blue-900 text-base font-medium">
                                    {employee?.updated_at ? format(employee?.updated_at, 'yyyy-MM-dd') : null}
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
