import { SubtleButton } from '@/components/buttons/SubtleButton';
import UnderlineHeading from '@/components/heading/UnderlineHeading';
import DocumentIcon from '@/components/icons/DocumentIcon';
import EditIcon from '@/components/icons/EditIcon';
import { Employee } from '@/types/employee.types';
import { format } from 'date-fns';

interface Props {
    employee: Employee | null;
}
export default function Specification({ employee }: Props) {
    return (
        <div className="mt-[21px] w-full">
            <div className="flex flex-row gap-6 justify-between items-center">
                <div className="w-full">
                    <div className="flex flex-row justify-between items-center">
                        <UnderlineHeading text="المؤهلات" />
                        <SubtleButton className="!text-sm font-medium tracking-normal !leading-[120%] text-white-blue-900 px-4 py-[9.5px]">
                            <DocumentIcon />
                            تحميل الشهادات
                        </SubtleButton>
                    </div>
                    <div className="w-full mt-2 min-w-[352px]">
                        <div className="flex flex-row items-center border-b border-b-white-blue-500">
                            <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[164px]">
                                الدرجة العلمية
                            </p>
                            <p className="flex flex-row items-center gap-4 p-2 text-primery-blue-900 text-base font-medium">
                                {employee?.education_degree}
                            </p>
                        </div>
                        <div className="flex flex-row items-center border-b border-b-white-blue-500">
                            <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[164px]">
                                الجامعة
                            </p>
                            <p className="flex flex-row items-center gap-4 p-2 text-primery-blue-900 text-base font-medium">
                                {employee?.university}
                            </p>
                        </div>
                        <div className="flex flex-row items-center border-b border-b-white-blue-500">
                            <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[164px]">
                                التخصص
                            </p>
                            <p className="flex flex-row items-center gap-4 p-2 text-primery-blue-900 text-base font-medium">
                                {employee?.department}
                            </p>
                        </div>
                    </div>
                </div>
                <div className="w-full">
                    <div className="flex flex-row justify-between items-center">
                        <UnderlineHeading text="الراتب" />
                        <SubtleButton className="!text-sm font-medium tracking-normal leading-[120%] text-white-blue-900 px-4 py-[9.5px]">
                            <EditIcon />
                            تعديل
                        </SubtleButton>
                    </div>
                    <div className="w-full mt-2 min-w-[352px]">
                        <div className="flex flex-row items-center border-b border-b-white-blue-500">
                            <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[164px]">
                                الراتب الأساسي
                            </p>
                            <p className="flex flex-row items-center gap-4 p-2 text-primery-blue-900 text-base font-medium">
                                {employee?.main_salary}
                            </p>
                        </div>
                        <div className="flex flex-row items-center border-b border-b-white-blue-500">
                            <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[164px]">
                                الراتب الإضافي
                            </p>
                            <p className="flex flex-row items-center gap-4 p-2 text-primery-blue-900 text-base font-medium">
                                1,200 ر.س
                            </p>
                        </div>
                        <div className="flex flex-row items-center border-b border-b-white-blue-500">
                            <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[164px]">
                                المكافآت
                            </p>
                            <p className="flex flex-row items-center gap-4 p-2 text-primery-blue-900 text-base font-medium">
                                1,200 ر.س
                            </p>
                        </div>
                    </div>
                </div>
                <div className="w-full">
                    <div className="flex flex-row justify-between items-center">
                        <UnderlineHeading text="الترقيات" />
                        <SubtleButton className="!text-sm font-medium tracking-normal leading-[120%] text-white-blue-900 px-4 py-[9.5px]">
                            <EditIcon />
                            تعديل
                        </SubtleButton>
                    </div>
                    <div className="w-full mt-2 min-w-[352px]">
                        <div className="flex flex-row items-center border-b border-b-white-blue-500">
                            <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[164px]">
                                الدرجة الحالية
                            </p>
                            <p className="flex flex-row items-center gap-4 p-2 text-primery-blue-900 text-base font-medium">
                                {employee?.last_position}
                            </p>
                        </div>
                        <div className="flex flex-row items-center border-b border-b-white-blue-500">
                            <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[164px]">
                                الترقية القادمة
                            </p>
                            <p className="flex flex-row items-center gap-4 p-2 text-primery-blue-900 text-base font-medium">
                                {employee?.next_upgrade}
                            </p>
                        </div>
                        <div className="flex flex-row items-center border-b border-b-white-blue-500">
                            <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-[164px]">
                                تاريخ الترقية
                            </p>
                            <p className="flex flex-row items-center gap-4 p-2 text-primery-blue-900 text-base font-medium">
                                {employee?.updated_at ? format(employee?.updated_at, 'yyyy-MM-dd') : null}
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
